import Vue from 'vue';
import Vuex from 'vuex';
import mutations from './mutations.js';
import actions from './actions.js';

Vue.use(Vuex);


const store=new Vuex.Store({
//状态数据
state:{
    topics:[
        {name:'all',desc:'全部'},
        {name:'good',desc:'精华'},
        {name:'share',desc:'分享'},
        {name:'ask',desc:'问答'},
        {name:'job',desc:'招聘'},
        {name:'dev',desc:'测试'}
      ],
      //未登录时显示更多菜单
      unLoginedMoreMenus:{
        login:'登录'
      },
      //登陆时显示更多菜单
      LoginedMoreMenus:{
        center:"个人中心",
        msg:"消息通知",
        logout:"登出",
        create:"发表新话题"
      },
      author_id:'',
      //登陆状态
      isLogined: false,
      //登陆用户名
      loginname:'',
      accesstoken:'',
      //
      isShowMoreMenus:false
},
getters:{
    moreMenus: function(state){
        if(state.isLogined){
          //用户处理登陆状态，返回登录后的更多菜单
          //vuex中的数据不能被直接修改，必须通过mutations修改
          return state.LoginedMoreMenus;
        }
        return state.unLoginedMoreMenus;
      }
},
mutations,
actions

});

export default store;


